.button {
  background-color: $basic-green;
  color: $white;
  cursor: pointer;
  text-decoration: none;
  border: none;
  text-transform: uppercase;
  text-align: center;
  @include font-line(16px,20px);
  &:hover {
    background-color: $hover-green;
  }
  &:active {
    color: rgba($white,0.3)
  }
}
